header {
  position: relative;
  max-width: 100%;
  width: 100%;
  position: fixed;
  transition: all 0.4s ease;
  top: 0;
  left: 0;
  z-index: 9999;
  right: 0;

}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  padding: 59px 0;
  background-color: transparent;
  transition: all 0.4s;
  z-index: 9999;
  position: relative;
  input {
    transition: all 0.4s ease;
    transform: translateY(-100%);
    opacity: 0;
    padding: 12px 15px;
    border: 1px solid rgba(70, 70, 70, 0.295);
    background-color: rgba(255, 255, 255, 0.589);
    box-shadow: 0 20px 50px rgba(88, 88, 88, 0.479);
    backdrop-filter: blur(.5rem);
    position: absolute;
    top: 10vh;
    left: 0;
    width: 1000px;
    width: 100%;
    z-index: 99;
    display: block;
    &:focus{
      outline-color: $blue;
    }
  }
  .activeInput {
    transform: translateY(0%);
    opacity: 1;
  }

  .logo {
    display: block;
    text-decoration: none;
    font-size: 3em;
    color: black;
    font-weight: bold;
    transition: all 0.4s;
  }
  .search {
    transition: all 0.4s ease;
    display: flex;
    align-items: center;
    gap: 15px;
    svg {
      cursor: pointer;
    }
  }

  .burger {
    cursor: pointer;
    div {
      width: 30px;
      height: 3px;
      background-color: black;
      margin: 4px 0;
      transition: all 0.4s ease-in-out;
    }
  }
}

.sticky{
  background-color: rgba(255, 255, 255, 0.589);
  box-shadow: 0 20px 50px rgba(88, 88, 88, 0.479);
  backdrop-filter: blur(.5rem);
}

ul {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: rgba(255, 255, 255, 0.781);
  backdrop-filter: blur(2.3px);
  -webkit-backdrop-filter: blur(2.3px);
  top: 10vh;
  right: -100%;
  width: 50%;
  height: 90vh;
  transition: all 0.5s ease-in-out;
  li {
    a {
      font-weight: bold;
      font-size: 24px;
      transition: all 0.4s;
      color: $lightblack;
      &:hover {
        color: black;
      }
    }
  }
}
.burger {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  .line3 {
    width: 18px;
    right: 0;
  }
}

.nav-active {
  right: 0%;
  width: 100%;
}

.toggle .line1 {
  width: 30px;
  transform: rotate(45deg) translateY(14px);
}

.toggle .line3 {
  width: 30px;
  transform: rotate(315deg) translateY(-14px);
}
