.ourWorks {
  h4 {
    font-family: Fira Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 4px;
    text-transform: uppercase;
  }
  .links {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    h3 {
      font-family: Fira Sans;
      font-style: normal;
      font-weight: 600;
      font-size: 40px;
      line-height: 50px;
      letter-spacing: -1px;
      @media(max-width: $breakpoint768){
        font-size: 34px;
      }
    }
    .linkAll {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 25px;
      @media(max-width:$breakpoint768){
        margin-top: 20px;
        flex: 1 0 100%;
      }
      a {
        font-family: Fira Sans;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 4px;
        text-transform: uppercase;
        color: $lightblack;
        &:first-child {
          color: blue;
        }
        @media(max-width: $breakpoint768){
          flex:0 0 5%;
          gap: 10px;
        }
      }
    }
  }
  .photos {
    margin-top: 108px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
    @media(max-width: $breakpoint1200){
      justify-content: space-evenly;
    }
    img {
      transition: all 0.4s ease;
      cursor: pointer;
      @media(max-width: $breakpoint768){
        flex: 0 1 38%;
      }
      &:hover {
        transform: translateY(-20px);
        box-shadow: 0 0 50px rgba(0, 0, 0, 0.438);
      }
    }
  }
  .footer_text {
    margin-top: 52px;
    margin-bottom: 20%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    h5 {
      max-width: 730px;
      width: 100%;
      font-family: Fira Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 28px;
      color: $lightblack;
      @media(max-width: $breakpoint992){
        margin-bottom: 20px;
      }
    }
    a {
      padding: 17px 24px;
      background-color: $blue;
      font-family: Fira Sans;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 24px;
      text-align: center;
      letter-spacing: 4px;
      text-transform: uppercase;
      color: white;
    }
  }
}
