#service {
  margin-top: 142px;
  @media (max-width: $breakpoint768) {
    margin-top: 70px;
  }

  .service_section {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .service_1 {
      max-width: 539px;
      width: 100%;
      @media (max-width: $breakpoint1200) {
        flex: 0 1 48%;
      }

      @media (max-width: $breakpoint768) {
        max-width: 95%;
        flex: 1 0 100%;
      }

      h1 {
        font-family: Fira Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 50px;
        letter-spacing: -1px;
        margin-top: 40px;
        @media (max-width: $breakpoint992) {
          font-size: 26px;
        }
      }
    }

    .list_section {
      display: flex;
      margin-top: 48px;
      width: 409px;
      @media (max-width: $breakpoint1200) {
        flex: 0 1 48%;
      }
      @media (max-width: $breakpoint768) {
        flex: 1 0 100%;
      }
      .first_list,
      .second_list {
        height: 197px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        a {
          font-family: Fira Sans;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 28px;
          color: $lightblack;
          transition: all 0.4s;
          &:hover {
            color: black;
          }
        }
        .m {
          margin: 21px 0;
        }
      }
      .first_list {
        margin-right: 60px;
      }
    }
  }
  h4 {
    font-family: Fira Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 4px;
    text-transform: uppercase;
  }

  .super__easy {
    margin-top: 181px;
    position: relative;
    .bg5 {
      position: absolute;
      z-index: -1;
      top: 0;
      @media(max-width: $breakpoint1200){
        width: 100%;
      }
    }
    .container {
      display: flex;
      align-items: center;
      gap: 20px;
      @media(max-width: $breakpoint1200){
        flex-wrap: wrap;
      }
      .photo{
        width: 48%;
        @media(max-width: $breakpoint1200){
          flex: 0 1 75%;
        }
        @media(max-width: $breakpoint992){
          flex: 0 1 100%;
        }
        .imgSec {
          @media(max-width: $breakpoint1200){
            width: 100%;
          }

          @media(max-width: $breakpoint768){
           transform: translateY(20%);
          }
        }
      }

      .eas {
        width: 473px;
        display: flex;
        flex-direction: column;
        @media(max-width: $breakpoint1200){
          flex: 0 1 90%;
        }
        @media(max-width: $breakpoint992){
          flex: 1 0 90%;
        }
        .super__title {
          font-family: Fira Sans;
          font-style: normal;
          font-weight: 600;
          font-size: 40px;
          line-height: 50px;
          letter-spacing: -1px;
        }
        .super__text {
          font-family: Fira Sans;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 28px;
          color: $lightblack;
          margin-top: 13px;
          @media(max-width: $breakpoint992){
            flex: 1 0 90%;
          }
        }
        .fle {
          display: flex;
          flex-direction: column;
          justify-content: center;
          margin-top: 62px;
          .svgIcon {
            width: 18px;
            padding: 17px;
            background-color: rgba(128, 128, 128, 0.205);
            border-radius: 50%;
          }
          a {
            display: flex;
            align-items: center;
            gap: 29px;
            margin-bottom: 30px;
            font-family: Fira Sans;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 24px;
            letter-spacing: 4px;
            text-transform: uppercase;
            color: $black;
          }
        }
      }
    }
  }
}
