.testimonials {
  .container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .about {
      @media (max-width: $breakpoint992) {
        flex: 0 0 30%;
      }

      @media (max-width: $breakpoint768) {
        flex: 1 0 100%;
      }

      h4 {
        font-family: Fira Sans;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 4px;
        text-transform: uppercase;
      }
      h1 {
        font-family: Fira Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 40px;
        line-height: 50px;
        letter-spacing: -1px;
        max-width: 540px;
        margin-top: 73px;
        margin-bottom: 121px;

        @media (max-width: $breakpoint768) {
          margin-bottom: 30px;;
        }
        @media (max-width: $breakpoint992) {
          font-size: 26px;
        }
      }

      .work {
        font-family: Fira Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 28px;
        color: $lightblack;
        margin-bottom: 13px;
      }
      .name {
        font-family: Fira Sans;
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 32px;
      }
    }
    .image {
      @media (max-width: $breakpoint768) {
        display: block;
        max-width: 100%;
        width: 100%;
        margin-top: 20px;
      }
      img {
        width: 100%;
        @media (max-width: $breakpoint768) {
          flex: 1 0 100%;
        }
      }
    }
  }
}
